import ComponentAbstract from 'System/Component/ComponentAbstract';

abstract class ElementComponent extends ComponentAbstract
{
    public componentElement: JQuery;

    public constructor(element?: Element | JQuery)
    {
        super();

        if (element)
        {
            if (element instanceof Element)
            {
                element = jQuery(element);
            }

            this.componentElement = element;
        }
    }

    protected addElementHandler(event: string, eventSelector: string | Function, eventHandler?: Function, context?: any): void
    {
        this.eventManager.addElementHandler(
            this.componentElement,
            event,
            eventSelector,
            eventHandler,
            context
        );
    }

    protected removeElementHandler(event: string, eventSelector: string | Function, eventHandler?: Function, context?: any): void
    {
        this.eventManager.removeElementHandler(
            this.componentElement,
            event,
            eventSelector,
            eventHandler,
            context
        );
    }
}

export default ElementComponent;


