import FieldComponentAbstract from 'Front/Field/Filter/FieldComponentAbstract';
import ISupportNotesComponent from 'Front/FilterNotes/ISupportNotesComponent';
import NoteEntry from 'Front/FilterNotes/Entry';
import EventManager from 'System/Event/EventManager';

import CreateElement from 'System/Helper/CreateElement';

export default class FilterComponent extends FieldComponentAbstract implements ISupportNotesComponent
{
    public get itemElements(): JQuery
    {
        return this.activeComponentElement.find('.wcpf-radio-item');
    }

    public initialProperties(): void
    {
        super.initialProperties();

        //this.resetDocumentElementValue(this.getFieldKey());
    }

    public attachElementEvents(manager: EventManager): void
    {
        super.attachElementEvents(manager);

        let trigger_selectors = [];

        if (this.componentElement.hasClass('wcpf-hierarchical-collapsed')) {
            trigger_selectors.push('.wcpf-radio-item.wcpf-item-hierarchical-collapsed:not(.disabled) > .wcpf-item-inner > .wcpf-item-label .wcpf-input-container');

            trigger_selectors.push('.wcpf-radio-item:not(.disabled,.wcpf-item-hierarchical-collapsed)> .wcpf-item-inner > .wcpf-item-label');
        } else {
            trigger_selectors.push('.wcpf-radio-item:not(.disabled) .wcpf-radio-label');
        }

        manager.addElementHandler(
            this.componentElement.find(trigger_selectors.join(',')),
            'click',
            this.onLabelClick
        );
    }

    protected onLabelClick(event: Event): void
    {
        let labelElement = CreateElement(event.currentTarget),
            itemElement = labelElement.parents('.wcpf-radio-item:first');

        this.itemElements.find('.wcpf-input')
            .prop('checked', false);

        this.itemElements.filter('.checked')
            .removeClass('checked');

        labelElement.find('.wcpf-input')
            .prop('checked', true);

        itemElement.addClass('checked');

        this.updateValueFromElement(
            this.getFieldKey()
        );
    }

    public setDocumentElementValue(filterKey: string, value: any): void
    {
        if (value == null)
        {
            value = '';
        }

        this.itemElements.find('.wcpf-input')
            .prop('checked', false);

        this.itemElements.filter('.checked')
            .removeClass('checked');

        let itemElement = this.itemElements.filter(`[data-item-key="${value}"]`),
            inputElement = itemElement.find(`.wcpf-input:first`);

        inputElement.prop('checked', true);

        itemElement.addClass('checked');
    }

    public getDocumentElementValue(filterKey: string): any
    {
        return this.itemElements.filter('.checked').data('item-key');
    }

    public removeFilterValueByFilterNote(noteEntry: NoteEntry): void
    {
        this.resetDocumentElementValue(this.getFieldKey());

        this.removeFilterValue(this.getFieldKey());
    }

    public collectFilterNotes(filterValue: any, filterKey: string): NoteEntry[]
    {
        let entries = [];

        if (filterValue != null && typeof filterValue == 'string')
        {
            let itemElement = this.itemElements.filter(`[data-item-key="${filterValue}"]`),
                labelElement = itemElement.find('.wcpf-radio-label:first');

            if (!itemElement.length) {
                return entries;
            }

            entries.push(
                new NoteEntry({
                    filterComponent: this,
                    filterValue: filterValue,
                    text: labelElement.find('.wcpf-title').html()
                })
            );
        }

        return entries;
    }
}


