import ComponentAbstract from 'Front/Component/ComponentAbstract';
import PackageAbstract from 'System/Package/PackageAbstract';
import EventManager from 'System/Event/EventManager';

import Entity from 'App/Entity/Entity';
import Project from 'Front/Project/Project';
import BaseComponent from 'Front/Filter/Component/BaseComponent';

export default class ComponentBuilder extends ComponentAbstract
{
    public initialProperties(): void
    {
        this.app.componentRegister.set('Filter/Builder/Component', this);
    }

    public attachEvents(manager: EventManager): void {}

    public createComponent(entity: Entity, project: Project): BaseComponent
    {
        if (!this.prototypes.component.has(entity.entityKey))
        {
            throw new Error(`Component prototype by entity key '${entity.entityKey}' not found`);
        }

        let componentPrototype = this.prototypes.component.get(entity.entityKey),
            filterComponent = this.componentBuilder.build(componentPrototype);

        filterComponent.entity = entity;

        filterComponent.project = project;

        for (let i in entity.childEntities)
        {
            let childEntity = entity.childEntities[i];

            filterComponent.childFilterComponents.push(
                this.createComponent(childEntity, project)
            );
        }

        this.register.components.set(entity.entityId, filterComponent);

        return filterComponent;
    }

    public implementationComponent(filterComponent: BaseComponent): void
    {
        this.componentBuilder.implementation(filterComponent, this.package);

        for (let i in filterComponent.childFilterComponents)
        {
            this.implementationComponent(filterComponent.childFilterComponents[i]);
        }
    }
}


