import ComponentAbstract from 'System/Component/ComponentAbstract';
import PackageAbstract from 'Package/PackageAbstract';

export default class ComponentBuilder
{
    public build(componentType: typeof ComponentAbstract, pack?: PackageAbstract): ComponentAbstract
    {
        let component = new (componentType as any)();

        if (pack)
        {
            this.implementation(component, pack);
        }

        return component as ComponentAbstract;
    }

    public implementation(component: ComponentAbstract, pack: PackageAbstract): void
    {
        component.package = pack;

        component.initialProperties();

        component.attachEvents(component.eventManager);
    }
}


