import PackageAbstract from 'System/Package/PackageAbstract';
import ComponentAbstract from 'System/Component/ComponentAbstract';
import ApplicationEvent from 'System/ApplicationEvent';

import ProjectPackage from 'Front/Project/Package';

import BaseComponent from 'Front/Filter/Component/BaseComponent';
import Dispatcher from 'Front/Filter/Dispatcher';
import ComponentBuilder from 'Front/Filter/Builder/ComponentBuilder';

export default class Package extends PackageAbstract
{
    public readonly initFilterEvent: ApplicationEvent<any> = new ApplicationEvent<any>();

    public initialProperties(): void
    {
        this.app.objectContainer.set('ProductsQueryVars', new Map<string, any>());

        this.app.objectContainer.set('ProductsLoopProperties', new Map<string, any>());

        this.app.packageRegister.set('Filter/Package', this);

        this.app.objectContainer.set('Filter/Components', new Map<any, BaseComponent>());

        this.app.objectContainer.set('Filter/Component/Prototypes', new Map<any, typeof BaseComponent>());
    }

    public getInitialSubpackages(): Object[]
    {
        return [
            new ProjectPackage()
        ];
    }

    public getInitialComponents(): ComponentAbstract[]
    {
        return [
            new Dispatcher(),
            new ComponentBuilder()
        ];
    }
}


