import ComponentAbstract from 'System/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import ExtendObject from 'System/Object/ExtendObject';

import Entity from 'App/Entity/Entity';
import EntityStatus from 'App/Entity/EntityStatus';

import IStructure from 'App/Entity/IStructure';
import IRegisterEntry from 'App/Entity/IRegisterEntry';


export default class Collector extends ComponentAbstract
{
    public get register(): Map<string, IRegisterEntry>
    {
        return this.app.objectContainer.get('Entity/Register') as Map<string, IRegisterEntry>;
    }

    public initialProperties(): void
    {
        this.app.componentRegister.set('Entity/Collector', this);
    }

    public attachEvents(eventManager: EventManager): void {}

    public collectVirtualEntityByKey(entityKey: string): Entity
    {
        let entity = null;

        if (!this.register.has(entityKey))
        {
            throw new Error(`Entry by key '${entityKey}' in register not found`);
        }

        let entry = this.register.get(entityKey);

        entity = new Entity();

        entity.status = EntityStatus.virtual;

        entity.entityId = this.createUniqueID();

        entity.parentId = 0;

        entity.entityKey = entry.id;

        entity.order = 0;

        entity.options = ExtendObject({}, entry.defaultOptions);

        entity.title = entry.label;

        entity.childEntities = [];

        return entity;
    }

    public collectEntityByStructure(structure: IStructure): Entity
    {
        let entity = null;

        entity = new Entity();

        entity.status = EntityStatus.published;

        entity.entityId = structure.entityId;

        entity.parentId = structure.parentId;

        entity.entityKey = structure.entityKey;

        entity.order = Number(structure.order);

        entity.options = ExtendObject({}, structure.options);

        entity.title = structure.title;

        entity.childEntities = [];

        if (structure.childEntities
            && structure.childEntities.hasOwnProperty('length')
            && structure.childEntities.length) {
            for (let i in structure.childEntities)
            {
                let itemStructure = structure.childEntities[i] as IStructure;

                let itemEntity = this.collectEntityByStructure(itemStructure);

                entity.childEntities.push(itemEntity);
            }
        }

        return entity;
    }

    public createUniqueID(): string
    {
        let chr4 = () => {
            return Math.random().toString(16).slice(-4);
        };

        return 'virtual-' + chr4() + chr4() +
            '-' + chr4() +
            '-' + chr4() +
            '-' + chr4() +
            '-' + chr4() + chr4() + chr4();
    }
}


