import FilteringComponentAbstract from 'Front/Filter/Component/FilteringComponentAbstract';
import EventManager from 'System/Event/EventManager';
import CreateElement from "../../../System/Helper/CreateElement";

abstract class FieldComponentAbstract extends FilteringComponentAbstract
{
    public getFieldKey(): string
    {
        return this.getFilterKeys().field;
    }

    public getFilterKeys(): any
    {
        return {
            field: this.options.get('optionKey')
        };
    }

    public updateValueFromElement(param: any): void
    {
        let filterKey = this.getFieldKey();

        if (typeof param == "string")
        {
            filterKey = param;
        }

        let fieldValue = this.getDocumentElementValue(filterKey);

        if ((Array.isArray(fieldValue) && !fieldValue.length)
            || fieldValue == null
            || (typeof fieldValue == 'string' && !fieldValue.length))
        {
            this.removeFilterValue(filterKey);
        }
        else
        {
            this.applyFilterValue(filterKey, fieldValue);
        }
    }

    public resetDocumentElementValue(filterKey: string): void
    {
        this.setDocumentElementValue(filterKey, null);
    }

    public initialPropertiesOfElement(): void
    {
        super.initialPropertiesOfElement();

        if (this.componentState.has('box')) {
            if (this.componentState.get('box') == 'hide') {
                this.componentElement.addClass('wcpf-box-hide');

                this.componentElement.find('.wcpf-content').slideUp(0);
            } else {
                this.componentElement.removeClass('wcpf-box-hide');

                this.componentElement.find('.wcpf-content').slideDown(0);
            }
        } else if (this.componentElement.hasClass('wcpf-box-style') && this.componentElement.hasClass('wcpf-box-hide')) {
            this.componentElement.find('.wcpf-content').slideUp(300);
        }

        if (this.componentElement.hasClass('wcpf-hierarchical-collapsed')) {
            this.componentElement.find('.wcpf-item-hierarchical-collapsed')
                .each((index, itemDomElement) => {
                    let itemElement = jQuery(itemDomElement),
                        boxIndex = 'boxItem_' + itemElement.data('item-key'),
                        isItemHidden = itemElement.hasClass('wcpf-item-box-hide');

                    if (this.componentState.has(boxIndex)
                        && this.componentState.get(boxIndex) != 'hide'
                        && isItemHidden) {
                        itemElement.removeClass('wcpf-item-box-hide');

                        itemElement.find('.wcpf-item-children-container:first').slideDown(0);
                    } else {
                        itemElement.find('.wcpf-item-children-container:first').css({
                            display: isItemHidden ? 'none' : 'block'
                        });
                    }
                });
        }

        if (this.componentElement.hasClass('wcpf-scrollbar')) {
            this.componentElement.each((index, componentDomElement) => {
                let componentElement = jQuery(componentDomElement),
                    contentElement = componentElement.find('.wcpf-content'),
                    maxHeightIndex = 'maxHeight_' + index;

                if (this.componentState.has(maxHeightIndex)) {
                    contentElement.css({
                        maxHeight: this.componentState.get(maxHeightIndex) + 'px'
                    });
                } else {
                    let heightOfVisibleContent = this.options.get('heightOfVisibleContent');

                    if (! heightOfVisibleContent || heightOfVisibleContent <= 0) {
                        heightOfVisibleContent = 12;
                    }

                    let firstLabelElement = componentElement.find('.wcpf-item-label:first'),
                        maxHeight = firstLabelElement.actual('height') * heightOfVisibleContent;

                    maxHeight += 2;

                    contentElement.css({
                        maxHeight: maxHeight + 'px'
                    });

                    this.componentState.set(maxHeightIndex, maxHeight);
                }
            });
        } else if (this.componentElement.hasClass('wcpf-contain-more-button') && !this.componentState.get('showMoreContent')) {
            this.componentElement.each((index, componentDomElement) => {
                let componentElement = jQuery(componentDomElement),
                    heightOfVisibleContent = this.options.get('heightOfVisibleContent'),
                    itemsElements = componentElement.find('.wcpf-item'),
                    moreButtonElement = componentElement.find('.wcpf-more-button');

                if (! heightOfVisibleContent || heightOfVisibleContent <= 0) {
                    heightOfVisibleContent = 12;
                }

                let currentItem = 1;

                itemsElements.each((itemIndex, itemDomElement) => {
                        let itemElement = jQuery(itemDomElement);

                        if (itemElement.parents('.wcpf-item.wcpf-item-box-hide').length) {
                            return;
                        }

                        if (currentItem > heightOfVisibleContent) {
                            itemElement.addClass('wcpf-more-item wcpf-item-hide');
                        }

                        currentItem++;
                    });

                if (currentItem > heightOfVisibleContent) {
                    moreButtonElement.addClass('wcpf-status-active');
                }
            });
        }
    }

    public attachElementEvents(manager: EventManager): void
    {
        super.attachElementEvents(manager);

        if (this.componentElement.hasClass('wcpf-box-style'))
        {
            manager.addElementHandler(
                this.componentElement,
                'click',
                '.wcpf-heading-label',
                this.onBoxToggleClick
            );
        }

        if (this.componentElement.hasClass('wcpf-hierarchical-collapsed')) {
            manager.addElementHandler(
                this.componentElement.find([
                    '.wcpf-item-hierarchical-collapsed > .wcpf-item-inner > .wcpf-item-label .wcpf-title-container',
                    '.wcpf-item-hierarchical-collapsed > .wcpf-item-inner > .wcpf-item-label .box-item-toggle'
                ].join(',')),
                'click',
                this.onItemBoxToggleClick
            );
        }

        if (this.componentElement.hasClass('wcpf-contain-more-button')) {
            manager.addElementHandler(
                this.componentElement.find('.wcpf-more-button'),
                'click',
                this.onMoreButtonClick
            );
        }
    }

    protected onMoreButtonClick(event: Event): void {
        let moreButtonElement = jQuery(event.currentTarget),
            componentElement = moreButtonElement.parents('.wcpf-front-element:first'),
            moreItemsElements = componentElement.find('.wcpf-more-item');

        if (componentElement.hasClass('wcpf-hide-more-items')) {
            moreItemsElements.addClass('wcpf-item-hide');
        } else {
            moreItemsElements.removeClass('wcpf-item-hide');
        }

        componentElement.toggleClass('wcpf-hide-more-items');

        this.componentState.set('showMoreContent', componentElement.hasClass('wcpf-hide-more-items'));
    }

    protected onItemBoxToggleClick(event: Event): void {
        let titleElement = CreateElement(event.currentTarget),
            itemElement = titleElement.parents('.wcpf-item-hierarchical-collapsed:first'),
            boxIndex = 'boxItem_' + itemElement.data('item-key');

        if (!itemElement.hasClass('wcpf-item-box-hide')) {
            itemElement.find('.wcpf-item-children-container:first').slideUp(300);

            this.componentState.set(boxIndex, 'hide');
        } else {
            itemElement.find('.wcpf-item-children-container:first').slideDown(300);

            this.componentState.set(boxIndex, 'show');
        }

        itemElement.toggleClass('wcpf-item-box-hide');
    }

    protected onBoxToggleClick(event: Event): void
    {
        this.componentElement.toggleClass('wcpf-box-hide');

        if (this.componentElement.hasClass('wcpf-box-hide'))
        {
            this.componentElement.find('.wcpf-content').slideUp(300);

            this.componentState.set('box', 'hide');
        }
        else
        {
            this.componentElement.find('.wcpf-content').slideDown(300);

            this.componentState.set('box', 'show');
        }
    }
}

export default FieldComponentAbstract;


