import IObserver from 'App/Option/IObserver';

import Context from 'System/Object/Context';

export default class Observable
{
    protected observers: Map<any, IObserver[]> = new Map<any, IObserver[]>();

    public add(optionKey: any, observer: IObserver, context?: any): void
    {
        if (!this.observers.has(optionKey))
        {
            this.observers.set(optionKey, []);
        }

        observer = context ? Context(observer, context) : observer;

        this.observers.get(optionKey).push(observer);
    }

    public remove(optionKey: any, observer: IObserver, context?: any): void
    {
        if (! this.observers.has(optionKey))
        {
            return;
        }

        observer = context ? context : Context(observer, this);

        this.observers.get(optionKey).filter((handler) => {
            return handler !== observer;
        });
    }

    public notify(optionKey: any, value: any, oldValue: any)
    {
        if (this.observers.has(optionKey))
        {
            this.observers.get(optionKey).forEach((callbacks) => {
                callbacks(value, oldValue, optionKey);
            });
        }
    }
}


