import Observable from 'App/Option/Observable';

import ObjectIndex from 'System/Object/ObjectIndex';

export default class ObjectRepository
{
    public readonly observable: Observable;

    public container: object;

    public has(key: any): boolean
    {
        return ObjectIndex(this.container, key) !== undefined;
    }

    public get(key: any): any
    {
        return ObjectIndex(this.container, key);
    }

    public set(key: any, value: any): void
    {
        let oldValue = this.has(key)? this.get(key): null;

        ObjectIndex(this.container, key, value);

        if (typeof key == 'string' && key.split('.').length)
        {
            key = key.split('.')[0];
        }

        this.observable.notify(key, value, oldValue);
    }

    public constructor(containerObject?: object)
    {
        this.observable = new Observable();

        if (containerObject)
        {
            this.container = containerObject;
        }
    }
}


