import ITreeEntity from 'System/Entity/ITreeEntity';

export default function GetEntitySelector(entity: ITreeEntity, endSelector?: string): string
{
    let selector: string = '';

    while (true)
    {
        let parentEntity = entity.getParentEntity();

        if (! entity.rootSelector)
        {
            if (! parentEntity)
            {
                break;
            }
            else
            {
                entity = parentEntity;

                continue;
            }
        }

        if (selector.length == 0)
        {
            selector = entity.rootSelector;
        }
        else
        {
            selector = entity.rootSelector + ' ' + selector;
        }

        if (! parentEntity)
        {
            break;
        }

        entity = parentEntity;
    }

    if (endSelector)
    {
        if (selector.length == 0)
        {
            selector = endSelector;
        }
        else
        {
            selector += ' ' + endSelector;
        }
    }

    return selector;
}



