import ICallback from 'System/Event/ICallback';
import ICallbackDesc from 'System/Event/ICallbackDesc';
import ITypedSubscription from 'System/Event/ITypedSubscription';

export default class ApplicationEvent<ArgType>
{
    public defaultContext: any = null;

    protected callbacks: ICallbackDesc<ArgType>[] = [];

    public subscribe(callback: ICallback<ArgType>, subscriber?: any): ITypedSubscription<ArgType>
    {
        if (! subscriber && this.defaultContext)
        {
            subscriber = this.defaultContext;
        }

        if (! subscriber)
        {
            subscriber = callback;
        }

        let that = this,
            entry: ITypedSubscription<ArgType> = {
                callback: callback,
                event: that,
                unsubscribe: function () {
                    that.unsubscribe(callback);
                }
            };

        this.callbacks.push({
            callback: callback,
            subscriber: subscriber
        });

        return entry;
    }

    public unsubscribe(callback: ICallback<ArgType>, subscriber?: any): void
    {
        if (! subscriber && this.defaultContext)
        {
            subscriber = this.defaultContext;
        }

        if (! subscriber)
        {
            subscriber = callback;
        }

        let filteredList: ICallbackDesc<ArgType>[] = [];

        for (let i = 0; i < this.callbacks.length; i++)
        {
            if (this.callbacks[i].callback !== callback && this.callbacks[i].subscriber !== subscriber)
            {
                filteredList.push(
                    this.callbacks[i]
                );
            }
        }

        this.callbacks = filteredList;
    }

    public trigger(arg?: ArgType, context?: any): void
    {
        for (let i = 0; i < this.callbacks.length; i++)
        {
            this.callbacks[i].callback.apply(
                this.callbacks[i].subscriber,
                [
                    arg,
                    context
                ]
            );
        }
    }
}



