import NavigationAbstract from 'Front/Project/URLNavigation/NavigationAbstract';

export default class QueryNavigation extends NavigationAbstract
{
    protected urlParams: Map<string, any>;

    public initialProperties(): void
    {
        super.initialProperties();

        this.urlParams = new Map<string, any>();

        this.updateUrlParams();
    }

    protected updateUrlParams(): void
    {
        this.urlParams.clear();

        let url = location.search.substring(1),
            pairs = url.split('&');

        if (!url.length)
        {
            return;
        }

        for (let i in pairs)
        {
            let pair = pairs[i].split('=');

            this.urlParams.set(
                pair[0],
                (pair.length > 1 ? this.decode(pair[1]) : undefined)
            );
        }
    }

    public hasAttribute(key: string): boolean
    {
        return this.urlParams.has(key);
    }

    public getAttribute(key: string): any
    {
        return this.urlParams.get(key);
    }

    public setAttribute(key: string, value: any): void
    {
        this.updateUrlParams();

        this.urlParams.set(key, value);

        this.updateUrl();
    }

    public removeAttribute(key: string): void
    {
        this.updateUrlParams();

        this.urlParams.delete(key);

        this.updateUrl();
    }

    protected updateUrl(): void
    {
        let params = [];

        for (let [key, value] of this.urlParams.entries())
        {
            if (value == undefined) {
                params.push(this.encode(key));
            } else {
                params.push(this.encode(key) + '=' + this.encode(value));
            }
        }

        let newUrl = location.pathname + location.hash;

        if (params.length)
        {
            newUrl = '?' + params.join('&');
        }

        window.history.replaceState({}, document.title, newUrl);
    }

    public decode(value: string): any
    {
        value = decodeURIComponent(value);

        if (value.indexOf(',') !== -1)
        {
            return value.split(',');
        }

        return value;
    }

    public encode(value: any): string
    {
        if (Array.isArray(value))
        {
            return value.join(',');
        }

        return value;
    }

    public setAllAttributesToUrl(url: string, attributes: any): string
    {
        let search = '';

        if (url.indexOf('?') != -1)
        {
            search = url.slice(url.indexOf('?') + 1);

            url = url.slice(0, url.indexOf('?'));
        }

        let params = {},
            pairs = search.indexOf('&') != -1 ? search.split('&') : [],
            urlNewParams = [];

        for (let i in pairs)
        {
            let pair = pairs[i].split('=');

            params[pair[0]] = this.decode(pair[1]);
        }

        for (let i in attributes)
        {
            params[i] = attributes[i];
        }

        for (let key in params)
        {
            let value = params[key];

            urlNewParams.push(this.encode(key) + '=' + this.encode(value));
        }

        if (urlNewParams.length)
        {
            url += '?' + urlNewParams.join('&');
        }

        return url;
    }

    public removeAllAttributesFromUrl(url: string, attributes: string[]): string
    {
        if (url.indexOf('?') == -1)
        {
            return url;
        }

        let search = url.slice(url.indexOf('?') + 1);

        if (!search.length)
        {
            return url;
        }

        url = url.slice(0, url.indexOf('?'));

        let params = {},
            pairs = search.split('&'),
            urlNewParams = [];

        for (let i in pairs)
        {
            let pair = pairs[i].split('=');

            params[pair[0]] = this.decode(pair[1]);
        }

        for (let i in attributes)
        {
            let item = attributes[i];

            if (params.hasOwnProperty(item))
            {
                delete params[item];
            }
        }

        for (let key in params)
        {
            let value = params[key];

            urlNewParams.push(this.encode(key) + '=' + this.encode(value));
        }

        if (urlNewParams.length)
        {
            url += '?' + urlNewParams.join('&');
        }

        return url;
    }

    public getAllAttributes(): any {
        this.updateUrlParams();

        let paris = {};

        for (let [key, value] of this.urlParams.entries()) {
            paris[key] = value;
        }

        return paris;
    }
}


