import FieldComponentAbstract from 'Front/Field/Filter/FieldComponentAbstract';
import ISupportNotesComponent from 'Front/FilterNotes/ISupportNotesComponent';
import NoteEntry from 'Front/FilterNotes/Entry';
import EventManager from 'System/Event/EventManager';

import CreateElement from 'System/Helper/CreateElement';

export default class FilterComponent extends FieldComponentAbstract implements ISupportNotesComponent
{
    public get itemElements(): JQuery
    {
        return this.activeComponentElement.find('.wcpf-box-item');
    }

    public attachElementEvents(manager: EventManager): void
    {
        super.attachElementEvents(manager);

        manager.addElementHandler(
            this.componentElement.find('.wcpf-box-item:not(.disabled)'),
            'click',
            this.onClickItem
        );
    }

    protected onClickItem(event: Event): void
    {
        let itemElement = CreateElement(event.currentTarget);

        if (!this.options.get('multiSelect'))
        {
            this.itemElements
                .filter('.selected')
                .not(`[data-value="${itemElement.data('value')}"]`)
                .removeClass('selected');
        }

        itemElement.toggleClass('selected');

        this.updateValueFromElement(this.getFieldKey());
    }

    public setDocumentElementValue(filterKey: string, value: any): void
    {
        if (value == null)
        {
            value = '';
        }

        this.itemElements.filter('.selected').removeClass('selected');

        if (typeof value == 'string')
        {
            value = [value];
        }

        if (Array.isArray(value))
        {
            for (let item of value)
            {
                this.itemElements.filter(`[data-value="${item}"]`).addClass('selected');
            }
        }
    }

    public getDocumentElementValue(filterKey: string): any
    {
        let list = [];

        this.itemElements.filter('.selected').each((index, domElement) => {
            let itemElement = CreateElement(domElement);

            list.push(itemElement.data('value'));
        });

        return list;
    }

    public removeFilterValueByFilterNote(noteEntry: NoteEntry): void
    {
        let values = this.getDocumentElementValue(this.getFieldKey()),
            itemIndex = values.indexOf(noteEntry.filterValue);

        if (itemIndex != -1)
        {
            values.splice(itemIndex, 1);

            this.setDocumentElementValue(this.getFieldKey(), values);

            if (values.length)
            {
                this.applyFilterValue(this.getFieldKey(), values);
            }
            else
            {
                this.removeFilterValue(this.getFieldKey());
            }
        }
    }

    public collectFilterNotes(filterValue: any, filterKey: string): NoteEntry[]
    {
        let entries = [];

        if (typeof filterValue == 'string')
        {
            filterValue = [filterValue];
        }

        if (Array.isArray(filterValue))
        {
            for (let item of filterValue.values())
            {
                let boxElement = this.itemElements.filter(`[data-value="${item}"]`),
                    titleElement = boxElement.find('.wcpf-title');

                if (boxElement.length && titleElement.length)
                {
                    entries.push(
                        new NoteEntry({
                            filterComponent: this,
                            filterValue: item,
                            text: titleElement.html()
                        })
                    );
                }
            }
        }

        return entries;
    }
}


