import BaseComponent from 'Front/Filter/Component/BaseComponent';
import IFilteringComponent from 'Front/Filter/Component/IFilteringComponent';

abstract class FilteringComponentAbstract extends BaseComponent implements IFilteringComponent
{
    public getFilterKeysList(): string[]
    {
        return (Object as any).values(this.getFilterKeys());
    }

    public getFilterKeyByIndex(filterIndex: string): string
    {
        let keys = this.getFilterKeys();

        if (keys.hasOwnProperty(filterIndex))
        {
            return keys[filterIndex];
        }

        return null;
    }

    public applyFilterValue(filterKey: string, value: any): void
    {
        this.project.applyFilterValue(this, filterKey, value);
    }

    public removeFilterValue(filterKey: string): void
    {
        this.project.removeFilterValue(this, filterKey);
    }

    public abstract getFilterKeys(): any;

    public abstract resetDocumentElementValue(filterKey: string): void;

    public abstract setDocumentElementValue(filterKey: string, value: any): void;

    public abstract getDocumentElementValue(filterKey: string): any;
}

export default FilteringComponentAbstract;


