import FilteringComponentAbstract from 'Front/Field/Filter/FieldComponentAbstract';
import EventManager from 'System/Event/EventManager';
import ISupportNotesComponent from 'Front/FilterNotes/ISupportNotesComponent';
import NoteEntry from 'Front/FilterNotes/Entry';

import Context from 'System/Object/Context';

export default class FilterComponent extends FilteringComponentAbstract implements ISupportNotesComponent
{
    public get minInputElement(): JQuery
    {
        return this.activeComponentElement.find('.wcpf-min-input');
    }

    public get maxInputElement(): JQuery
    {
        return this.activeComponentElement.find('.wcpf-max-input');
    }

    public get sliderElement(): JQuery
    {
        return this.componentElement.find('.priceSliderInput');
    }

    public get labelElement(): JQuery
    {
        return this.componentElement.find('.priceLabel');
    }

    public getFilterKeys(): any
    {
        let filterKeys: any = {};

        if (this.options.get('optionKeyFormat') == 'dash')
        {
            filterKeys.price = this.options.get('optionKey');
        }
        else if (this.options.get('optionKeyFormat') == 'two')
        {
            filterKeys.minPrice = this.options.get('minPriceOptionKey');

            filterKeys.maxPrice = this.options.get('maxPriceOptionKey');
        }

        return filterKeys;
    }

    public initialPropertiesOfElement(): void
    {
        super.initialPropertiesOfElement();

        this.sliderElement.show();

        if (this.componentElement.hasClass('wcpf-display-price-label'))
        {
            this.labelElement.show();
        }

        let min = parseFloat(this.minInputElement.data('min')),
            max = parseFloat(this.maxInputElement.data('max')),
            minValue = parseFloat(this.minInputElement.val() as string),
            maxValue = parseFloat(this.maxInputElement.val() as string);

        this.sliderElement.slider({
            range: true,
            animate: true,
            min: min,
            max: max,
            values: [minValue, maxValue],
            stop: Context(this.onSlideStop, this),
            slide: Context(this.onSlideMove, this)
        });

        this.setPriceLabel(minValue, maxValue);

        if (this.componentElement.hasClass('wcpf-box-style') && this.componentElement.hasClass('wcpf-box-hide'))
        {
            this.componentElement.find('.wcpf-content').slideUp(300);
        }
    }

    public attachElementEvents(manager: EventManager): void
    {
        super.attachElementEvents(manager);

        this.attachMinMaxInputEvents();

        this.eventManager.addElementHandler(
            this.componentElement,
            'keypress keyup blur',
            '.wcpf-price-slider-min-max-inputs .wcpf-input',
            this.onMinMaxInputKeyChange
        );
    }

    protected attachMinMaxInputEvents(): void
    {
        this.eventManager.addElementHandler(
            this.componentElement,
            'change',
            '.wcpf-price-slider-min-max-inputs .wcpf-input',
            this.onMinMaxInputChange
        );
    }

    protected detachMinMaxInputEvents(): void
    {
        this.eventManager.removeElementHandler(
            this.componentElement,
            'change',
            '.wcpf-price-slider-min-max-inputs .wcpf-input',
            this.onMinMaxInputChange
        );
    }

    public resetDocumentElementValue(filterKey: string): void
    {
        let min = parseFloat(this.minInputElement.data('min')),
            max = parseFloat(this.maxInputElement.data('max'));

        if (this.getFilterKeyByIndex('price') == filterKey)
        {
            this.setDocumentElementValue(filterKey, min + '-' + max);
        }
        else if (this.getFilterKeyByIndex('minPrice') == filterKey)
        {
            this.setDocumentElementValue(filterKey, min);
        }
        else if (this.getFilterKeyByIndex('maxPrice') == filterKey)
        {
            this.setDocumentElementValue(filterKey, max);
        }
    }

    public setDocumentElementValue(filterKey: string, value: any): void
    {
        this.detachMinMaxInputEvents();

        if (this.getFilterKeyByIndex('price') == filterKey)
        {
            let items = value.split('-');

            this.minInputElement.val(items[0]);

            if (items.length > 1)
            {
                this.maxInputElement.val(items[1]);
            }
        }
        else if (this.getFilterKeyByIndex('minPrice') == filterKey)
        {
            this.minInputElement.val(value);
        }
        else if (this.getFilterKeyByIndex('maxPrice') == filterKey)
        {
            this.maxInputElement.val(value);
        }

        this.updateSliderRangeValue();

        this.attachMinMaxInputEvents();
    }

    public getDocumentElementValue(filterKey: string): any
    {
        let min = this.getMinValue(),
            max = this.getMaxValue();

        if (this.getFilterKeyByIndex('price') == filterKey)
        {
            return min + '-' + max;
        }
        else if (this.getFilterKeyByIndex('minPrice') == filterKey)
        {
            return min;
        }
        else if (this.getFilterKeyByIndex('maxPrice') == filterKey)
        {
            return max;
        }

        return '';
    }

    protected updateSliderRangeValue(): void
    {
        let min = parseFloat(this.minInputElement.val() as any),
            max = parseFloat(this.maxInputElement.val() as any);

        this.sliderElement.slider('values', [min, max]);

        this.setPriceLabel(min, max);
    }

    protected onMinMaxInputKeyChange(event: KeyboardEvent): void
    {
        let inputElement = jQuery(event.currentTarget),
            value = inputElement.val() as string;

        this.detachMinMaxInputEvents();

        inputElement.val(
            value.replace(/[^\d].+/, '')
        );

        if (event.which < 48 || event.which > 57)
        {
            event.preventDefault();
        }

        this.attachMinMaxInputEvents();
    }

    protected onMinMaxInputChange(event: Event): void
    {
        let inputElement = jQuery(event.currentTarget),
            filterKey = null;

        if (this.options.get('optionKeyFormat') == 'dash')
        {
            filterKey = this.getFilterKeyByIndex('price');
        }
        else if (this.options.get('optionKeyFormat') == 'two')
        {
            filterKey = inputElement.hasClass('wcpf-min-input')
                ? this.getFilterKeyByIndex('minPrice')
                : this.getFilterKeyByIndex('maxPrice');
        }

        this.updateSliderRangeValue();

        if (filterKey)
        {
            this.applyFilterValue(filterKey, this.getDocumentElementValue(filterKey));
        }
    }

    protected onSlideMove(event: Event, ui: any): void
    {
        this.minInputElement.val(ui.values[0]);

        this.maxInputElement.val(ui.values[1]);

        this.updateSliderRangeValue();
    }

    protected onSlideStop(event: Event, ui: any): void
    {
        let filterKey = null;

        if (this.options.get('optionKeyFormat') == 'dash')
        {
            filterKey = this.getFilterKeyByIndex('price');
        }
        else if (this.options.get('optionKeyFormat') == 'two')
        {
            let isMaxHandle = jQuery(ui.handle).is(':last-child');

            filterKey = isMaxHandle ? this.getFilterKeyByIndex('maxPrice') : this.getFilterKeyByIndex('minPrice');
        }

        if (filterKey)
        {
            this.applyFilterValue(filterKey, this.getDocumentElementValue(filterKey));
        }
    }

    public getPriceText(min: any): string
    {
        return (window as any).accounting.formatMoney(min, {
            symbol: this.wooData.priceFormat.currencyFormatSymbol,
            decimal: this.wooData.priceFormat.currencyFormatDecimalSep,
            thousand: this.wooData.priceFormat.currencyFormatThousandSep,
            precision: this.wooData.priceFormat.currencyFormatNumDecimals,
            format: this.wooData.priceFormat.currencyFormat
        });
    }

    public setPriceLabel(min: any, max: any): void
    {
        let accounting = (window as any).accounting;

        this.labelElement.find('.from').html(this.getPriceText(min));

        this.labelElement.find('.to').html(this.getPriceText(max));
    }

    public removeFilterValueByFilterNote(noteEntry: NoteEntry): void
    {
        for (let filterKey of this.getFilterKeysList())
        {
            this.resetDocumentElementValue(filterKey);

            this.removeFilterValue(filterKey);
        }
    }

    public getMaxValue(): string
    {
        let maxValue = this.maxInputElement.val() as string;

        if (maxValue == '')
        {
            maxValue = this.maxInputElement.data('max');
        }

        return maxValue;
    }

    public getMinValue(): string
    {
        let minValue = this.minInputElement.val() as string;

        if (minValue == '')
        {
            minValue = this.minInputElement.data('min');
        }

        return minValue;
    }

    public collectFilterNotes(filterValue: any, filterKey: string): NoteEntry[]
    {
        let entries = [];

        if (filterValue != null && typeof filterValue == 'string')
        {
            let min = this.getMinValue(),
                max = this.getMaxValue();

            entries.push(
                new NoteEntry({
                    filterComponent: this,
                    filterValue: filterValue,
                    text: this.getPriceText(min) + ' - ' + this.getPriceText(max)
                })
            );
        }

        return entries;
    }
}


