import PackageAbstract from 'System/Package/PackageAbstract';
import Application from 'System/Application';

export default class PackageBuilder
{
    public buildByClass(packageClass: any, app: Application, parent?: PackageAbstract): PackageAbstract
    {
        let packageObject = this.createPackageByClass(packageClass, app, parent);

        return this.build(packageObject, app, parent);
    }

    public build(packageObject: PackageAbstract, app: Application, parent?: PackageAbstract): PackageAbstract
    {
        packageObject.app = app;

        packageObject.parentPackage = parent;


        this.createInteraction(packageObject, app, parent);

        this.afterInteractionBuilding(packageObject, app);


        this.packageInitialProperties(packageObject);

        this.packageAttachEvents(packageObject);

        packageObject.collectAfter.trigger(packageObject);

        return packageObject;
    }

    protected packageAttachEvents(packageObject: PackageAbstract): void
    {
        packageObject.componentList.forEach(
            componentItem => {
                componentItem.attachEvents(
                    componentItem.eventManager
                );
            }
        );

        packageObject.subpackageList.forEach(
            packageItem => {
                this.packageAttachEvents(packageItem);
            }
        );
    }

    protected packageInitialProperties(packageObject: PackageAbstract): void
    {
        packageObject.initialProperties();

        packageObject.componentList.forEach(
            componentItem => {
                componentItem.initialProperties();
            }
        );

        packageObject.subpackageList.forEach(
            packageItem => {
                this.packageInitialProperties(packageItem);
            }
        );
    }

    protected createInteraction(packageObject: any, app: Application, parent?: PackageAbstract): void
    {
        let subpackgeList = packageObject.getInitialSubpackages(),
            componentList = packageObject.getInitialComponents();

        // Add components to storage

        if (componentList)
        {
            componentList.forEach(
                componentItem => {
                    componentItem.package = packageObject;

                    packageObject.componentList.add(componentItem);
                }
            );
        }

        // Add packages to storage

        if (subpackgeList)
        {
            subpackgeList.forEach(
                packageClassItem => {
                    let packageItem = null;

                    if (packageClassItem instanceof PackageAbstract)
                    {
                        packageItem = packageClassItem;

                        packageItem.app = app;

                        packageItem.parentPackage = packageObject;
                    }
                    else
                    {
                        packageItem = this.createPackageByClass(packageClassItem, packageObject.app, packageObject);
                    }

                    packageObject.subpackageList.add(packageItem);
                }
            );

            packageObject.subpackageList.forEach(
                packageItem => {
                    this.createInteraction(packageItem, packageItem.app, packageItem.parentPackage);
                }
            );
        }
    }

    protected createPackageByClass(packageClass: any, app: Application, parent?: PackageAbstract): PackageAbstract
    {
        if (! (packageClass.prototype instanceof PackageAbstract))
        {
            throw new TypeError('Сlass does not implement class "PackageAbstract"');
        }

        let packageObject = new packageClass();

        packageObject.app = app;

        packageObject.parentPackage = parent;

        return packageObject;
    }

    protected afterInteractionBuilding(packageObject: any, app: Application): void
    {
        if (packageObject.afterInteractionBuilding)
        {
            packageObject.afterInteractionBuilding(app);
        }

        if (packageObject.componentList.size)
        {
            packageObject.componentList.forEach(
                componentItem => {
                    if (componentItem.afterInteractionBuilding)
                    {
                        componentItem.afterInteractionBuilding(app);
                    }
                }
            );
        }

        if (packageObject.subpackageList.size)
        {
            packageObject.subpackageList.forEach(
                subpackageItem => {
                    this.afterInteractionBuilding(subpackageItem, app);
                }
            );
        }
    }
}


