import ComponentAbstract from 'Front/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

abstract class NavigationAbstract extends ComponentAbstract
{
    public navigationOptions: object = {};

    public abstract hasAttribute(key: string): boolean;

    public abstract getAttribute(key: string): any;

    public abstract setAttribute(key: string, value: any): void;

    public abstract removeAttribute(key: string): void;

    public abstract removeAllAttributesFromUrl(url: string, attributes: string[]): string;

    public abstract setAllAttributesToUrl(url: string, attributes: any): string;

    public abstract decode(value: string): any;

    public abstract encode(value: any): string;

    public abstract getAllAttributes(): any;

    public attachEvents(manager: EventManager): void {}
}

export default NavigationAbstract;


