import FieldComponentAbstract from 'Front/Field/Filter/FieldComponentAbstract';
import ISupportNotesComponent from 'Front/FilterNotes/ISupportNotesComponent';
import NoteEntry from 'Front/FilterNotes/Entry';
import EventManager from 'System/Event/EventManager';

import CreateElement from 'System/Helper/CreateElement';

export default class FilterComponent extends FieldComponentAbstract implements ISupportNotesComponent
{
    public get itemElements(): JQuery
    {
        return this.activeComponentElement.find('.wcpf-checkbox-item');
    }

    public attachElementEvents(manager: EventManager): void
    {
        super.attachElementEvents(manager);

        let trigger_selectors = [];

        if (this.componentElement.hasClass('wcpf-hierarchical-collapsed')) {
            trigger_selectors.push('.wcpf-checkbox-item.wcpf-item-hierarchical-collapsed:not(.disabled) > .wcpf-item-inner > .wcpf-item-label .wcpf-input-container');

            trigger_selectors.push('.wcpf-checkbox-item:not(.disabled,.wcpf-item-hierarchical-collapsed)> .wcpf-item-inner > .wcpf-item-label');
        } else {
            trigger_selectors.push('.wcpf-checkbox-item:not(.disabled) .wcpf-checkbox-label');
        }

        manager.addElementHandler(
            this.componentElement.find(trigger_selectors.join(',')),
            'click',
            this.onLabelClick
        );
    }

    protected onLabelClick(event: Event): void
    {
        let labelElement = CreateElement(event.currentTarget),
            itemElement = labelElement.parents('.wcpf-checkbox-item:first');

        itemElement.toggleClass('checked');

        labelElement.find('.wcpf-input')
            .prop('checked', itemElement.hasClass('checked'));

        itemElement.parents('.wcpf-checkbox-item.checked').each(this.uncheckElement);

        itemElement.find('.wcpf-checkbox-item.checked').each(this.uncheckElement);

        this.updateValueFromElement(
            this.getFieldKey()
        );
    }

    protected uncheckElement(elementIndex, domElement): void {
        let element = jQuery(domElement),
            key = element.data('item-key');

        element.removeClass('checked');

        element.find(`.wcpf-input[value="${key}"]`)
            .prop('checked', false);
    }

    public setDocumentElementValue(filterKey: string, value: any): void
    {
        if (value == null)
        {
            value = '';
        }

        this.itemElements.find('.wcpf-input')
            .prop('checked', false);

        this.itemElements.filter('.checked')
            .removeClass('checked');

        if (typeof value == 'string')
        {
            value = [value];
        }

        if (Array.isArray(value))
        {
            for (let item of value)
            {
                let inputElement = this.itemElements.find(`.wcpf-input[value="${item}"]`),
                    itemElement = inputElement.parents('.wcpf-checkbox-item:first');

                inputElement.prop('checked', true);

                itemElement.addClass('checked');
            }
        }
    }

    public getDocumentElementValue(filterKey: string): any
    {
        let list = [];

        this.itemElements.filter('.checked').each((index, domElement) => {
            let itemElement = CreateElement(domElement);

            list.push(itemElement.data('item-key'));
        });

        return list;
    }

    public removeFilterValueByFilterNote(noteEntry: NoteEntry): void
    {
        let values = this.getDocumentElementValue(this.getFieldKey()),
            itemIndex = values.indexOf(noteEntry.filterValue);

        if (itemIndex != -1)
        {
            values.splice(itemIndex, 1);

            this.setDocumentElementValue(this.getFieldKey(), values);

            if (values.length)
            {
                this.applyFilterValue(this.getFieldKey(), values);
            }
            else
            {
                this.removeFilterValue(this.getFieldKey());
            }
        }
    }

    public collectFilterNotes(filterValue: any, filterKey: string): NoteEntry[]
    {
        let entries = [];

        if (typeof filterValue == 'string')
        {
            filterValue = [filterValue];
        }

        if (Array.isArray(filterValue))
        {
            for (let item of filterValue.values())
            {
                let itemElement = this.itemElements.filter(`[data-item-key="${item}"]`),
                    labelElement = itemElement.find('.wcpf-checkbox-label:first');

                if (itemElement.length && labelElement.length)
                {
                    entries.push(
                        new NoteEntry({
                            filterComponent: this,
                            filterValue: item,
                            text: labelElement.find('.wcpf-title').html()
                        })
                    );
                }
            }
        }

        return entries;
    }
}


