import ComponentAbstract from 'System/Component/ComponentAbstract';
import ICallback from 'System/Event/ICallback';
import CreateElement from 'System/Helper/CreateElement';
import GetEntitySelector from 'System/Helper/GetEntitySelector';
import Context from 'System/Object/Context';
import ApplicationEvent from 'System/ApplicationEvent';
import ITreeEntity from 'System/Entity/ITreeEntity';

export default class EventManager
{
    public readonly component: ComponentAbstract;

    protected rootElement: JQuery;

    public constructor(component: ComponentAbstract)
    {
        this.component = component;

        this.rootElement = CreateElement(document.documentElement);
    }

    public addWindowHandler(event: string, eventHandler: Function, context?: any): void
    {
        this.applyElementHandler(
            true,
            {
                element: CreateElement(window),
                event: event,
                eventSelector: null,
                eventHandler: eventHandler,
                context: context
            }
        );
    }

    public removeWindowHandler(event: string, eventHandler: Function, context?: any): void
    {
        this.applyElementHandler(
            false,
            {
                element: CreateElement(window),
                event: event,
                eventSelector: null,
                eventHandler: eventHandler,
                context: context
            }
        );
    }

    public addSystemHandler<T>(event: ApplicationEvent<T>, eventHandler: ICallback<T>, context?: any): void
    {
        context = this.getContext(context);

        event.subscribe(eventHandler, context);
    }

    public removeSystemHandler<T>(event: ApplicationEvent<T>, eventHandler: ICallback<T>, context?: any): void
    {
        context = this.getContext(context);

        event.unsubscribe(eventHandler, context);
    }

    public addElementHandler(
        element: Element | JQuery,
        event: string,
        eventSelector: string | Function,
        eventHandler?: Function,
        context?: any
    ): void
    {
        this.applyElementHandler(
            true,
            {
                element: element,
                event: event,
                eventSelector: eventSelector,
                eventHandler: eventHandler,
                context: context
            }
        );
    }

    public removeElementHandler(
        element: Element | JQuery,
        event: string,
        eventSelector: string | Function,
        eventHandler?: Function,
        context?: any
    ): void
    {
        this.applyElementHandler(
            false,
            {
                element: element,
                event: event,
                eventSelector: eventSelector,
                eventHandler: eventHandler,
                context: context
            }
        );
    }

    protected applyElementHandler(
        isActivated: boolean,
        options: {
            element: Element | JQuery,
            event: string,
            eventSelector: string | Function,
            eventHandler?: Function,
            context?: any
        }
    ): void
    {
        options.context = this.getContext(options.context);

        if (options.eventSelector instanceof Function && ! options.eventHandler)
        {
            options.eventHandler = options.eventSelector;

            options.eventSelector = '';
        }

        if (options.context)
        {
            options.eventHandler = Context(options.eventHandler, options.context);
        }

        if (options.element instanceof Element)
        {
            options.element = jQuery(options.element);
        }

        if (isActivated)
        {
            if (options.eventSelector)
            {
                options.element.on(options.event, options.eventSelector, options.eventHandler as any);
            }
            else
            {
                options.element.on(options.event, options.eventHandler as any);
            }
        }
        else
        {
            if (options.eventSelector)
            {
                options.element.off(options.event, options.eventSelector as string, options.eventHandler as any);
            }
            else
            {
                options.element.off(options.event, options.eventHandler as any);
            }
        }
    }

    protected getContext(context: any): any
    {
        if (! context && this.component)
        {
            context = this.component;
        }

        return context;
    }

    public addEntityHandler(
        entity: ITreeEntity,
        event: string,
        eventHandlerOrSelector: Function | string,
        contextOrHanlder?: any,
        context?: any
    ): void
    {
        let endSelector = '',
            eventHandler = eventHandlerOrSelector;

        if (typeof eventHandlerOrSelector == 'string' && contextOrHanlder instanceof Function)
        {
            endSelector = eventHandlerOrSelector;

            eventHandler = contextOrHanlder;
        }

        if (! context && contextOrHanlder && !(contextOrHanlder instanceof Function))
        {
            context = contextOrHanlder;
        }

        context = this.getContext(context);

        if (context)
        {
            eventHandler = Context(eventHandler, context);
        }

        let selector: string = GetEntitySelector(entity, endSelector);

        this.rootElement.on(event, selector, eventHandler as any);
    }

    public removeEntityHandler(
        entity: ITreeEntity,
        event: string,
        eventHandlerOrSelector: Function | string,
        contextOrHanlder?: any,
        context?: any
    ): void
    {
        let endSelector = '',
            eventHandler = eventHandlerOrSelector;

        if (typeof eventHandlerOrSelector == 'string' && contextOrHanlder instanceof Function)
        {
            endSelector = eventHandlerOrSelector;

            eventHandler = contextOrHanlder;
        }

        if (! context && contextOrHanlder && !(contextOrHanlder instanceof Function))
        {
            context = contextOrHanlder;
        }

        context = this.getContext(context);

        if (context)
        {
            eventHandler = Context(eventHandler, context);
        }

        let selector: string = GetEntitySelector(entity, endSelector);

        this.rootElement.off(event, selector, eventHandler as any);
    }

    public addComponentHandler(event: string, eventHandlerOrSelector: Function | string, contextOrHandler?: any, context?: any): void
    {
        this.addEntityHandler(
            this.component as any,
            event,
            eventHandlerOrSelector,
            contextOrHandler,
            context
        );
    }

    public removeComponentHandler(event: string, eventHandlerOrSelector: Function | string, contextOrHandler?: any, context?: any): void
    {
        this.removeEntityHandler(
            this.component as any,
            event,
            eventHandlerOrSelector,
            contextOrHandler,
            context
        );
    }

    public addPackageHandler(event: string, eventHandlerOrSelector: Function | string, contextOrHandler?: any, context?: any): void
    {
        this.addEntityHandler(
            this.component.package as any,
            event,
            eventHandlerOrSelector,
            contextOrHandler,
            context
        );
    }

    public removePackageHandler(event: string, eventHandlerOrSelector: Function | string, contextOrHandler?: any, context?: any): void
    {
        this.removeEntityHandler(
            this.component.package as any,
            event,
            eventHandlerOrSelector,
            contextOrHandler,
            context
        );
    }

    public addPageElementHandler(event: string, eventSelector: string | Function, eventHandler?: Function, context?: any): void
    {
        this.applyElementHandler(
            true,
            {
                element: this.rootElement,
                event: event,
                eventSelector: eventSelector,
                eventHandler: eventHandler,
                context: context
            }
        );
    }

    public removePageElementHandler(event: string, eventSelector: string | Function, eventHandler?: Function, context?: any): void
    {
        this.applyElementHandler(
            false,
            {
                element: this.rootElement,
                event: event,
                eventSelector: eventSelector,
                eventHandler: eventHandler,
                context: context
            }
        );
    }
}


