import ComponentAbstract from 'Front/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';

import BaseComponent from 'Front/Filter/Component/BaseComponent';

export default class Registration extends ComponentAbstract
{
    public attachEvents(manager: EventManager): void
    {
        manager.addSystemHandler(this.filterPackage.initFilterEvent, this.initFilter);
    }

    public initFilter(prototypes: any): void
    {
        prototypes.component.set('ColumnsLayout', BaseComponent)
    }
}


