import Application from 'System/Application';
import ComponentAbstract from 'System/Component/ComponentAbstract';
import ApplicationEvent from 'System/ApplicationEvent';

abstract class PackageAbstract
{
    public app: Application;

    public parentPackage: PackageAbstract;

    public readonly componentList: Set<ComponentAbstract> = new Set<ComponentAbstract>();

    public readonly subpackageList: Set<PackageAbstract> = new Set<PackageAbstract>();

    public readonly collectAfter: ApplicationEvent<PackageAbstract> = new ApplicationEvent<PackageAbstract>();

    public initialProperties(): void {}

    public getInitialSubpackages(): object[]
    {
        return [];
    }

    public abstract getInitialComponents(): ComponentAbstract[]

}

export default PackageAbstract;


