import BaseComponent from 'Front/Filter/Component/BaseComponent';
import EventManager from 'System/Event/EventManager';

export default class FilterComponent extends BaseComponent
{
    public attachElementEvents(manager: EventManager): void
    {
        super.attachElementEvents(manager);

        manager.addElementHandler(
            this.componentElement,
            'click',
            '.wcpf-button',
            this.onButtonClick
        );
    }

    protected onButtonClick(event: Event): void
    {
        let action = this.options.get('action');

        if (action == 'filter')
        {
            this.project.applyFilters();
        }
        else if (action == 'reset')
        {
            this.project.resetFilters();
        }
    }
}


