import CloneVariable from 'System/Object/CloneVariable';

export default function ObjectIndex(repository: object, path: any, value?: any)
{
    if (typeof path == 'string')
    {
        return ObjectIndex(repository, path.split('.'), value);
    }
    else if (path.length == 1 && value !== undefined)
    {
        return repository[path[0]] = value;
    }
    else if (path.length==0)
    {
        return CloneVariable(repository);
    }
    else
    {
        if (!repository.hasOwnProperty(path[0]))
        {
            if (value !== undefined)
            {
                repository[path[0]] = {};
            }
            else
            {
                return undefined;
            }
        }

        return ObjectIndex(
            repository[path[0]],
            path.slice(1),
            value
        );
    }
}


