import PackageAbstract from 'System/Package/PackageAbstract';
import ComponentAbstract from 'System/Component/ComponentAbstract';

import CheckBoxListPackage from 'Front/Field/CheckBoxList/Package';
import RadioListPackage from 'Front/Field/RadioList/Package';
import DropDownListPackage from 'Front/Field/DropDownList/Package';
import ButtonPackage from 'Front/Field/Button/Package';
import PriceSliderPackage from 'Front/Field/PriceSlider/Package';
import BoxListPackage from 'Front/Field/BoxList/Package';
import TextListPackage from 'Front/Field/TextList/Package';
import ColorListPackage from 'Front/Field/ColorList/Package';

export default class Package extends PackageAbstract
{
    public getInitialSubpackages(): object[]
    {
        return [
            new CheckBoxListPackage(),
            new RadioListPackage(),
            new DropDownListPackage(),
            new ButtonPackage(),
            new PriceSliderPackage(),
            new BoxListPackage(),
            new TextListPackage(),
            new ColorListPackage()
        ];
    }

    public getInitialComponents(): ComponentAbstract[]
    {
        return [];
    }
}


