import PackageAbstract from 'System/Package/PackageAbstract';
import EventManager from 'System/Event/EventManager';
import Application from 'System/Application';

abstract class ComponentAbstract
{
    public get app(): Application
    {
        return this.package.app;
    }

    public package: PackageAbstract;

    public eventManager: EventManager;

    public constructor(eventManager?: EventManager)
    {
        if (eventManager)
        {
            this.eventManager = eventManager;
        }
        else
        {
            this.eventManager = new EventManager(this);
        }
    }

    public initialProperties(): void {}

    public abstract attachEvents(eventManager: EventManager): void
}

export default ComponentAbstract;



