import PackageAbstract from 'System/Package/PackageAbstract';
import ComponentAbstract from 'System/Component/ComponentAbstract';

import URLNavigationPackage from 'Front/Project/URLNavigation/Package';

import Registration from 'Front/Project/Registration';

export default class Package extends PackageAbstract
{
    public initialProperties(): void
    {
        this.app.packageRegister.set('Project/Package', this);
    }

    public getInitialSubpackages(): Object[]
    {
        return [
            new URLNavigationPackage()
        ];
    }

    public getInitialComponents(): ComponentAbstract[]
    {
        return [
            new Registration()
        ];
    }
}


