import ElementComponent from 'Front/Component/ElementComponent';
import EventManager from 'System/Event/EventManager';

import Project from 'Front/Project/Project';
import Entity from 'App/Entity/Entity';
import ObjectRepository from 'App/Option/ObjectRepository';

export default class BaseComponent extends ElementComponent
{
    public entity: Entity;

    public project: Project;

    public childFilterComponents: BaseComponent[];

    public readonly options: ObjectRepository;

    protected componentState: Map<string, any>;

    public constructor()
    {
        super();

        this.options = new ObjectRepository();

        this.childFilterComponents = [];

        this.componentState = new Map<string, any>();
    }

    public initialProperties(): void
    {
        this.options.container = this.entity.options;

        this.initialPropertiesOfElement();
    }

    public attachEvents(manager: EventManager): void {
        this.attachElementEvents(manager);
    }

    public initialPropertiesOfElement(): void {}

    public attachElementEvents(manager: EventManager): void {}

    protected get activeComponentElement(): JQuery
    {
        if (this.componentElement.length > 1)
        {
            let activeElement = this.componentElement.filter(':visible');

            activeElement.each((index, domElement) => {
                let itemElement = jQuery(domElement);

                if (itemElement.css('visibility') == 'visible') {
                    activeElement = itemElement;

                    return false;
                }
            });

            return activeElement;
        }

        return this.componentElement;
    }
}


