import ElementComponent from 'Front/Component/ElementComponent';
import EventManager from 'System/Event/EventManager';

import ObjectCompare from 'System/Object/Compare';
import ArrayCompare from 'System/Object/ArrayCompare';
import CreateElement from 'System/Helper/CreateElement';
import CreateElementByEvent from 'System/Helper/CreateElementByEvent';

import Entry from 'Front/FilterNotes/Entry';
import Project from 'Front/Project/Project';

export default class Notes extends ElementComponent
{
    public project: Project;

    public get notesListElement(): JQuery
    {
        return this.componentElement.find('.wcpf-note-list');
    }

    public get notesItemsElement(): JQuery
    {
        return this.notesListElement.find('.wcpf-note-item');
    }

    public attachEvents(manager: EventManager): void
    {
        manager.addElementHandler(
            this.componentElement,
            'click',
            '.remove-filter',
            this.onRemoveFilterClick
        );

        this.eventManager.addSystemHandler(
            this.project.applyFilterValueEvent,
            this.onApplyFilterValue
        );

        this.eventManager.addSystemHandler(
            this.project.removeFilterValueEvent,
            this.onRemoveFilterValue
        );
    }

    protected onRemoveFilterValue(props: any): void
    {
        let filterComponent = props.filterComponent;

        this.notesItemsElement.each((index, domElement) => {
            let noteElement = CreateElement(domElement),
                noteEntry = noteElement.data('wcpf-note-entry');

            if (filterComponent.entity.entityId == noteEntry.filterComponent.entity.entityId)
            {
                noteElement.remove();
            }
        });
    }

    protected onApplyFilterValue(props: any): void
    {
        let filterComponent = props.filterComponent;

        if ('collectFilterNotes' in filterComponent)
        {
            let newEntries = filterComponent.collectFilterNotes(props.value, props.filterKey),
                isEmptyEntries = !newEntries.length;

            this.notesItemsElement.each((index, domElement) => {
                    let noteElement = CreateElement(domElement),
                        noteEntry = noteElement.data('wcpf-note-entry'),
                        indexNoteInFilters = -1;

                    if (filterComponent.entity.entityId != noteEntry.filterComponent.entity.entityId)
                    {
                        return;
                    }

                    for (let [i, newEntry] of newEntries.entries())
                    {
                        let valueEquals = false;

                        if (typeof newEntry.filterValue != typeof noteEntry.filterValue)
                        {
                            continue;
                        }
                        else if (Array.isArray(newEntry.filterValue)
                            && ArrayCompare(newEntry.filterValue, noteEntry.filterValue))
                        {
                            valueEquals = true;
                        }
                        else if (typeof newEntry.filterValue == 'object'
                            && ObjectCompare(newEntry.filterValue, noteEntry.filterValue))
                        {
                            valueEquals = true;
                        }
                        else
                        {
                            valueEquals = newEntry.filterValue == noteEntry.filterValue;
                        }

                        if (valueEquals && newEntry.mark == noteEntry.mark)
                        {
                            indexNoteInFilters = i as any;

                            break;
                        }
                    }

                    if (indexNoteInFilters == -1 || isEmptyEntries)
                    {
                        noteElement.remove();
                    }
                    else
                    {
                        newEntries.splice(indexNoteInFilters, 1);
                    }
                });

            if (newEntries.length)
            {
                for (let entry of newEntries.values())
                {
                    this.addEntry(entry);
                }
            }
        }
    }

    protected onRemoveFilterClick(event: Event): void
    {
        let targetElement = CreateElementByEvent(event),
            noteElement = targetElement.parents('.wcpf-note-item:first'),
            noteEntry = noteElement.data('wcpf-note-entry');

        if (noteEntry && noteEntry.filterComponent && 'removeFilterValueByFilterNote' in noteEntry.filterComponent)
        {
            noteEntry.filterComponent.removeFilterValueByFilterNote(noteEntry);
        }
    }

    public addEntry(entry: Entry): void
    {
        let templateElement = CreateElement(`
            <div class="wcpf-note-item">
                <span class="note-text">${entry.text}</span>
                <span class="remove-filter"></span>
            </div>
        `);

        templateElement.data('wcpf-note-entry', entry);

        this.notesListElement.append(templateElement);
    }
}


