import ComponentAbstract from 'Front/Component/ComponentAbstract';
import EventManager from 'System/Event/EventManager';
import ApplicationEvent from 'System/ApplicationEvent';

import ExtendObject from 'System/Object/ExtendObject';
import CreateElement from 'System/Helper/CreateElement';
import ObjectRepository from 'App/Option/ObjectRepository';

import FilterComponent from 'Front/Filter/Component/BaseComponent';
import IApplyFilterValueEventArgs from 'Front/Project/EventArgs/IApplyFilterValueEventArgs';
import IRemoveFilterValueEventArgs from 'Front/Project/EventArgs/IRemoveFilterValueEventArgs';
import IProductPageLoadingEventArgs from 'Front/Project/EventArgs/IProductPageLoadingEventArgs';
import IProductPageLoadedEventArgs from 'Front/Project/EventArgs/IProductPageLoadedEventArgs';
import IFilteringComponent from 'Front/Filter/Component/IFilteringComponent';
import ProjectComponent from 'Front/Project/FilterComponent';
import QueryNavigation from 'Front/Project/URLNavigation/QueryNavigation';

export default class Project extends ComponentAbstract {
    public initEvent: ApplicationEvent<Project>;

    public productPageLoadedEvent: ApplicationEvent<IProductPageLoadedEventArgs>;

    public beforeLoadingProductPageEvent: ApplicationEvent<IProductPageLoadingEventArgs>;

    public afterLoadingProductPageEvent: ApplicationEvent<IProductPageLoadingEventArgs>;

    public applyFilterValueEvent: ApplicationEvent<IApplyFilterValueEventArgs>;

    public removeFilterValueEvent: ApplicationEvent<IRemoveFilterValueEventArgs>;

    public projectComponent: ProjectComponent;

    public componentKeys: Map<string, IFilteringComponent>;

    protected urlNavigator: QueryNavigation;

    protected productPageRequest: any;

    protected activeFilters;

    protected pageUrl: string;

    public constructor() {
        super();

        this.initEvent = new ApplicationEvent<Project>();

        this.productPageLoadedEvent = new ApplicationEvent<IProductPageLoadedEventArgs>();

        this.beforeLoadingProductPageEvent = new ApplicationEvent<IProductPageLoadingEventArgs>();

        this.afterLoadingProductPageEvent = new ApplicationEvent<IProductPageLoadingEventArgs>();

        this.applyFilterValueEvent = new ApplicationEvent<IApplyFilterValueEventArgs>();

        this.removeFilterValueEvent = new ApplicationEvent<IRemoveFilterValueEventArgs>();

        this.productPageRequest = null;

        this.activeFilters = {};

        this.componentKeys = new Map<string, IFilteringComponent>();
    }

    public get projectId(): any {
        return this.projectComponent.entity.entityId;
    }

    public get projectOptions(): ObjectRepository {
        return this.projectComponent.options;
    }

    public initialProperties(): void {
        this.pageUrl = this.app.objectContainer.get('PageUrl');

        this.urlNavigator = this.componentBuilder.build(QueryNavigation, this.package);

        this.urlNavigator.navigationOptions = ExtendObject(
            this.urlNavigator.navigationOptions,
            this.projectOptions.get('urlNavigationOptions')
        );
    }

    public attachEvents(manager: EventManager): void {
        manager.addSystemHandler(this.initEvent, this.onInit);

        if (this.app.objectContainer.get('IsPaged')) {
            this.eventManager.addSystemHandler(this.beforeLoadingProductPageEvent, this.resetPaginationUrl);
        }
    }

    protected resetPaginationUrl() {
        let attributes = this.urlNavigator.getAllAttributes();

        if (attributes && attributes.hasOwnProperty('product-page')) {
            delete attributes['product-page'];
        }

        if (attributes && attributes.hasOwnProperty('paged')) {
            delete attributes['paged'];
        }

        let newUrl = this.urlNavigator.setAllAttributesToUrl(this.pageUrl, attributes);

        this.replacePageUrl(newUrl);

        this.eventManager.removeSystemHandler(this.beforeLoadingProductPageEvent, this.resetPaginationUrl);
    }

    protected onInit(): void {
        this.eachComponents((filterComponent) => {
            if (!('getFilterKeys' in filterComponent)) {
                return;
            }

            let keys = filterComponent.getFilterKeys();

            for (let filterKey of (<any>Object).values(keys)) {
                if (this.urlNavigator.hasAttribute(filterKey)) {
                    let filterValue = this.urlNavigator.getAttribute(filterKey);

                    this.activeFilters[filterKey] = filterValue;

                    this.applyFilterValueEvent.trigger({
                        filterComponent: filterComponent,
                        filterKey: filterKey,
                        value: filterValue
                    });
                }

                this.componentKeys.set(filterKey, filterComponent);
            }
        });

        let urlAttributes = Object.keys(this.activeFilters);

        urlAttributes.push('product-page');

        urlAttributes.push('paged');

        this.pageUrl = this.urlNavigator.removeAllAttributesFromUrl(this.pageUrl, urlAttributes);
    }

    public removeFilterValue(filterComponent: IFilteringComponent, filterKey: string): void {
        if (!this.activeFilters.hasOwnProperty(filterKey)) {
            return;
        }

        delete this.activeFilters[filterKey];

        this.removeFilterValueEvent.trigger({
            filterComponent: filterComponent,
            filterKey: filterKey
        });

        if (this.projectOptions.get('filteringStarts') == 'auto') {
            this.urlNavigator.removeAttribute(filterKey);

            this.applyFilters();
        }
    }

    public applyFilterValue(filterComponent: IFilteringComponent, filterKey: string, value: any): void {
        this.activeFilters[filterKey] = value;

        this.applyFilterValueEvent.trigger({
            filterComponent: filterComponent,
            filterKey: filterKey,
            value: value
        });

        if (this.projectOptions.get('filteringStarts') == 'auto') {
            this.urlNavigator.setAttribute(filterKey, value);

            this.applyFilters();
        }
    }

    public resetFilters(): void {
        for (let filterKey in this.activeFilters) {
            this.urlNavigator.removeAttribute(filterKey);

            if (this.componentKeys.has(filterKey)) {
                this.removeFilterValueEvent.trigger({
                    filterComponent: this.componentKeys.get(filterKey),
                    filterKey: filterKey
                });
            }
        }

        this.activeFilters = {};

        this.applyFilters();
    }

    public applyFilters(properties?: any): void {
        properties = ExtendObject({
            updateFiltersElements: true,
            applyAttributes: true
        }, typeof properties == 'object' ? properties : {});

        if (this.projectOptions.get('filteringStarts') != 'auto' && properties.applyAttributes) {
            for (let attribute in this.urlNavigator.getAllAttributes()) {
                if (this.componentKeys.has(attribute) && !this.activeFilters.hasOwnProperty(attribute)) {
                    this.urlNavigator.removeAttribute(attribute);
                }
            }

            for (let filterKey in this.activeFilters) {
                this.urlNavigator.setAttribute(filterKey, this.activeFilters[filterKey]);
            }
        }

        if (this.productPageRequest) {
            this.productPageRequest.abort();
        }

        this.beforeLoadingProductPageEvent.trigger({
            filters: this.activeFilters,
            project: this
        });

        this.app.windowElement.trigger('wcpf_before_ajax_filtering');

        this.productPageRequest = jQuery.ajax({
            url: location.href,
            type: 'GET'
        }).done((responseHtml) => {
            let responsePage = document.implementation.createHTMLDocument(document.title);

            responsePage.documentElement.innerHTML = responseHtml;

            if (properties.updateFiltersElements) {
                this.updateComponentElementsFromPage(responsePage);
            }

            this.productPageLoadedEvent.trigger({
                page: responsePage,
                filters: this.activeFilters,
                project: this
            });

            this.app.windowElement.trigger('wcpf_update_products');

            let scriptAfterProductsUpdate = (window as any).WCPFData.scriptAfterProductsUpdate;

            if (scriptAfterProductsUpdate && scriptAfterProductsUpdate.length) {
                eval(scriptAfterProductsUpdate);
            }
        }).always(() => {
            this.afterLoadingProductPageEvent.trigger({
                filters: this.activeFilters,
                project: this
            });

            this.app.windowElement.trigger('wcpf_after_ajax_filtering');
        });
    }

    public goToPageNumberByUrl(url: string): void {
        this.eventManager.removeSystemHandler(this.beforeLoadingProductPageEvent, this.resetPaginationUrl);

        this.replacePageUrl(url);

        this.applyFilters({
            updateFiltersElements: false,
            applyAttributes: false
        });

        this.eventManager.addSystemHandler(this.beforeLoadingProductPageEvent, this.resetPaginationUrl);
    }

    public applySortingToProducts(sorting: string): void {
        this.urlNavigator.setAttribute('orderby', sorting);

        this.applyFilters({
            updateFiltersElements: false,
            applyAttributes: false
        });
    }

    protected replacePageUrl(url: string): void {
        window.history.replaceState({}, document.title, url);
    }

    protected eachComponents(handler: (filterComponent: any) => void): void {
        let each = (filterComponent: any) => {
            handler(filterComponent);

            for (let i in filterComponent.childFilterComponents) {
                each(filterComponent.childFilterComponents[i]);
            }
        };

        each(this.projectComponent);
    }

    protected updateComponentElementsFromPage(page: Document): void {
        let documentElement = CreateElement(page.documentElement),
            projectComponentElement = documentElement.find(`.wcpf-front-element-${this.projectId}`);

        this.projectComponent.componentElement.replaceWith(projectComponentElement.first());

        this.projectComponent.componentElement = this.app.bodyElement.find(`.wcpf-front-element-${this.projectId}`);

        this.eachComponents((filterComponent) => {
            if (filterComponent.entity.entityId == this.projectId) {
                return;
            }

            filterComponent.componentElement = this.projectComponent.componentElement.find(`.wcpf-front-element-${filterComponent.entity.entityId}`);
        });

        this.eachComponents((filterComponent) => {
            if (!filterComponent.componentElement.length) {
                return;
            }

            filterComponent.initialPropertiesOfElement();

            filterComponent.attachElementEvents(filterComponent.eventManager);
        });
    }
}


