import ComponentAbstract from 'System/Component/ComponentAbstract';
import PackageAbstract from 'System/Package/PackageAbstract';
import CreateElement from 'System/Helper/CreateElement';

export default class Application
{
    public readonly objectContainer: Map<any, any> = new Map<any, any>();

    public readonly componentRegister: Map<any, ComponentAbstract> = new Map<any, ComponentAbstract>();

    public readonly packageRegister: Map<any, PackageAbstract> = new Map<any, PackageAbstract>();

    public get bodyElement(): JQuery
    {
        return CreateElement(document.body);
    }

    public get windowElement(): JQuery
    {
        return CreateElement(window);
    }

    public initialization() {}
}



